@import "./reset.css";


.App {min-height: 100vh; padding-bottom: 30px; box-sizing: border-box; background: #eee;}

/* ---------- Header ---------- */

.header {width: 100%; height: 80px; display: flex; justify-content: space-between; align-items: center; padding: 0 30px; box-sizing: border-box; background: #000;}
.header h1 {font-size: 24px; font-weight: bold; color: #fff;}

/* // ---------- Header ---------- */



/* ---------- contents ---------- */

.list-item {width: 100%; padding: 30px 30px; box-sizing: border-box; background: #fff; border: 1px solid #fff; border-bottom: 1px solid #ddd;}
.list-item:last-child {border-bottom: none !important;}
.list-item .list-title {display: flex; justify-content: space-between; align-items: center; margin-bottom: 30px;}
.list-item h2 {font-size: 18px; font-weight: bold; color: #000;}
.list-item span {margin-left: 10px; cursor: pointer; color: #000;}
.list-item p {line-height: 1.25; overflow: hidden; display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical; color: #333;}
.list-bottom {width: 100%; height: 30px; display: flex; justify-content: space-between; align-items: center; margin-top: 30px;}
.list-bottom button {width: calc(50% - 5px); height: 100%; border: none; cursor: pointer;}
.list-bottom button.delete {background: #bbb; color: #000;}
.list-bottom button.delete:active {background: #999;}
.list-bottom button.detail {background: #ffc83d; color: #000;}
.list-bottom button.detail:active {background: #ffb700;}

.board {padding-top: 30px; background: #fff;}
.board .board-top {width: 100%; height: 30px; display: flex; justify-content: space-between; align-items: center; background: #ffc83d;}
.board .board-top h2 {padding: 0 20px; box-sizing: border-box; font-weight: bold; color: #000; font-size: 14px;}
.board .board-top button {height: 100%; background: none; border: none; padding: 0 20px; box-sizing: border-box; cursor: pointer; font-size: 14px; color: #000;}
.board .board-detail {padding: 30px; box-sizing: border-box; background: #fff; border-bottom: 2px solid #ddd;}
.board .board-detail h3 {font-size: 18px; font-weight: bold; margin-bottom: 30px;}
.board .board-detail p {font-size: 16px; margin-top: 20px; line-height: 1.25;}

.input_body {width: 100%; height: 150px; margin-top: 30px; padding: 0 20px; box-sizing: border-box; display: flex; justify-content: space-between; align-items: center;}
.input_body .form_wrap {width: 80%; height: 100%; display: flex; justify-content: start; align-items: center; flex-direction: column;}
.input_body .form_wrap input, 
.input_body .form_wrap textarea {width: 100%; height: auto; outline: none; border: 1px solid #bbb; padding: 10px; box-sizing: border-box; background: #fff;}
.input_body .form_wrap input {height: 30px; margin-bottom: 10px;}
.input_body .form_wrap textarea {flex: auto;}
.input_body .form_wrap textarea:focus {box-sizing: border-box;}
.input_body .form_wrap input:focus, 
.input_body .form_wrap textarea:focus {border: 2px solid #ffc83d; border-radius: 0;}
.input_body .form_wrap input::placeholder, 
.input_body .form_wrap textarea::placeholder {color: #bbb;}
.input_body button {flex: auto; height: 100%; outline: none; border: none; padding: 0; box-sizing: border-box; background: #ffc83d; margin-left: 10px; cursor: pointer; color: #000;}
.input_body button:active {background: #ffb700;}

/* // ---------- contents ---------- */



/* ---------- Etc ---------- */

body::-webkit-scrollbar {
  width: 5px;  /* 스크롤바의 너비 */
}

body::-webkit-scrollbar-thumb {
  height: 30%; /* 스크롤바의 길이 */
  background: #bbb; /* 스크롤바의 색상 */
  border-radius: 10px;
}

body::-webkit-scrollbar-track {
  background: #eee;  /*스크롤바 뒷 배경 색상*/

}
/* // ---------- Etc ---------- */